.sidebarRow {
  display: flex;
  align-items: center;
  padding: 5px;
  cursor: pointer;
}
.sidebarRow:hover {
  background-color: rgba(222, 234, 241, 0.473);
  border-radius: 10px;
}
.sidebarRow > h4 {
  margin-left: 10px;
  font-weight: 550;
}

.sidebarRow > .MuiSvgIcon-root {
  font-size: x-large;
  color: rgb(0, 162, 255);
}
